.is-paper-animated, .is-paper-shadow-animated {
  animation: paper 80s steps(80, end) infinite; }

.is-text-animated {
  animation: text 2s infinite; }

.is-printer-animated {
  animation: printer 0.4s infinite; }

.is-shadows-animated {
  animation: shadows 0.4s infinite; }

@keyframes printer {
  0%, 85% {
    transform: perspective(10000px) rotateX(60deg) rotateZ(-35deg) translateZ(-5.6vw) scaleX(0.8) rotateY(0deg); }

  30%, 50%, 80% {
    transform: perspective(10000px) rotateX(60deg) rotateZ(-35deg) translateZ(-5.8vw) scaleX(0.8) rotateY(-0.5deg); }

  60%, 100% {
    transform: perspective(10000px) rotateX(60deg) rotateZ(-35deg) translateZ(-5.8vw) scaleX(0.8) rotateY(0.5deg); } }

@keyframes shadows {
  0%, 85% {
    transform: perspective(10000px) rotateX(60deg) rotateZ(-35deg) translateZ(-6vw) scaleX(0.8); }

  30%, 50%, 80% {
    transform: perspective(10000px) rotateX(60deg) rotateZ(-35deg) translateZ(-6vw) scaleX(0.81); }

  60%, 100% {
    transform: perspective(10000px) rotateX(60deg) rotateZ(-35deg) translateZ(-6vw) scaleX(0.79); } }

@keyframes text {
  10%, 90% {
    opacity: 1; } }

@keyframes paper {
  20% {
    bottom: -12.5vw;
    height: 15vw;
    opacity: 1; }

  50% {
    bottom: -7.5vw;
    height: 15vw;
    opacity: 1; }

  100% {
    bottom: -20vw;
    height: 15vw;
    opacity: 1; } }






